const uni = require('uni').default;















var app= require("../../common/common.js"); 
var id;
export default{
	data:{
		pageLoad:false, 
		pageData:{}
	},
	onLoad:function(option){
		id=option.id;
		this.getPage();
	},
	onReady:function(){
		uni.setNavigationBarTitle({
			title:"发布帖子",
		})
	} ,
	methods:{
		getPage:function(){
			var that=this;
			uni.request({
				url:app.apiHost+"?m=forum&ajax=1&a=add",
				success:function(data){
					that.pageLoad=true;
					that.pageData=data.data.data;
					 
				}
			})
		} 
	},
}



